
import './App.css';
// import './table.css'
import { Table, DatePicker, Select, Button, InputNumber, Layout, Row, Col, Space, message } from 'antd'
import axios from 'axios';
import { useEffect, useState, useRef } from 'react';
import { io } from 'socket.io-client';



const { RangePicker } = DatePicker
function App() {
    
    const [swapForm, setSwapForm] = useState({ currencyFrom: 'BTC', amountFrom: 0, currencyTo: 'USD'})
    const [dateFilter, setDateFilter] = useState({ fromDate: '', toDate: ''})
    const [exchangeType, setExchangeType] = useState(0)
    const [currencyPrice, setCurrencyPrice] = useState('')
    const [dateSource, setDateSource] = useState([])
    const [totalPages, setTotalPages] = useState(1);
    const [loading, setLoading] = useState(false);
    const [socketData, setSocketData] = useState(null);
    const socket = useRef();
    const onChange = (date, dateString) => {
        console.log(date, dateString);
      };
      const [messageApi, contextHolder] = message.useMessage();
      const success = () => {
        messageApi.open({
          type: 'info',
          content: 'Exchange submitted',
          className: 'custom-class',
          icon: '',
        });
      };
    const handleSwapClick = () => {
        setLoading(true);
        axios
        .post('http://localhost:3000/exchange',
            {
                "currencyFrom": swapForm.currencyFrom,
                "amountFrom": swapForm.amountFrom,
                "currencyTo": swapForm.currencyTo,
                "amountTo": currencyPrice
            } 
        ).then((res) => {
            setDateSource(res.data.data);
            setTotalPages(res.data.metadata.total);
            setLoading(false);
        });

    }

    function handleDatePickerFrom(date) {
        setDateFilter({
            fromDate: new Date(date.$d),
            toDate: dateFilter.toDate
        })
    }

    function handleDatePickerTo(date) {
        setDateFilter({
            fromDate: dateFilter.fromDate,
            toDate: new Date(date.$d)
        })
    }

    function handleFilterClick() {
        fetchRecords(1, dateFilter.fromDate, dateFilter.toDate, exchangeType);
    }


    const columns = [
        { title : 'Date & Time', dataIndex: 'created_at' },
        { title : 'Currency From', dataIndex: 'currencyFrom' },
        { title : 'Amount 1', dataIndex: 'amountFrom' },
        { title : 'Currency To', dataIndex: 'currencyTo' },
        { title : 'Amount 2', dataIndex: 'amountTo' },
        { title : 'Type', dataIndex: 'type', render: (text) => {
            if (text == 'Live Price') {
                return <span style={{color: '#5DBE7E'}}>{text}</span>
            } else {
                return <span style={{color: '#6368DF'}}>{text}</span>
            }
        } },
    ];
    
    useEffect(() => {
        fetchRecords(1, '', '', '');

        socket.current = io('http://localhost:3000', {
            transport: ["websocket"],
        });
        socket.current.on("price", (data) => {
            setSocketData(data)
            const temp = data.filter((i) => i.fiat == 'USD' && i.crypto == 'BTC')
            setCurrencyPrice(temp[0].amount)
        });
        
      }, []);
    

    const fetchRecords = (page, fromDate, toDate, type) => {
        setLoading(true);
        axios
        .get(`http://localhost:3000/exchange?pageNumber=${page}&type=${type}&fromDate=${fromDate}&toDate=${toDate}`)
        .then((res) => {
            setDateSource(res.data.data);
            setTotalPages(res.data.metadata.total);
            setLoading(false);
        });
    };

    return (

        <Layout>
            
            <div className="App ">
                <div className='header'>
                    
                    <div className='container'>
                        <div className='head'>
                            <h2>Exchange</h2>
                        </div>
                        <Row justify="left">
                        <Col span={4}>
                            <label className='label-head'>Currency form</label>
                            <Select
                                showSearch
                                placeholder="Curreny"
                                defaultValue={'BTC'}
                                optionFilterProp="children"
                                addOnBefore="Currency"
                                className='box-form'
                                onChange={value => setSwapForm({ currencyFrom: value, amountFrom: swapForm.amountFrom, currencyTo: swapForm.currencyTo })}
                                filterOption={(input, option) =>
                                (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                                }
                                options={[
                                    {
                                        value: 'BTC',
                                        label: 'Bitcoin',
                                    },
                                    {
                                        value: 'ETH',
                                        label: 'Ethereum',
                                    },
                                ]}
                            />
                            </Col>
                            <Col span={3}>
                            <label className='label-head'>Amount</label>
                            <InputNumber className='box-form'
                                onChange={value => setSwapForm({ currencyFrom: swapForm.currencyFrom, amountFrom: value, currencyTo: swapForm.currencyTo })}
                            />
                            </Col>
                            <Col span={4}>
                            <label className='label-head'>Currency to</label>
                            <Select
                                
                                showSearch
                                placeholder="Curreny"
                                optionFilterProp="children"
                                defaultValue={'USD'}
                                className='box-form btn-tus'
                                onChange={value => setSwapForm({ currencyFrom: swapForm.currencyFrom, amountFrom: swapForm.amountFrom, currencyTo: value })}
                                filterOption={(input, option) =>
                                (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                                }
                                options={[
                                {
                                    value: 'USD',
                                    label: 'USD',
                                },
                                {
                                    value: 'EUR',
                                    label: 'EUR',
                                },
                                ]}
                            />
                            </Col>
                            <Col span={3}>
                            <label className='label-head'>Amount</label>
                            <InputNumber className='box-form' value={currencyPrice ? currencyPrice : 'loading...'} />
                            </Col>
                            <Col span={2} className="btn">
                            {contextHolder}
                            <Button onClick={handleSwapClick}>Save</Button>
                            </Col>

                        </Row>
                    </div>
                </div>
                <div className="history">
                    <div className='container'>
                        <div className='head'>
                            <h2>History</h2>
                        </div>
                        <div className='body'>
                            <Row justify="left">
                            <Col span={4}>
                                <label className='label-head'>From date</label>
                                    <Space direction="vertical">
                                        <DatePicker onChange={handleDatePickerFrom} />
                                    </Space>
                                
                                </Col>
                                <Col span={4}>
                                <label className='label-head'>To date</label>
                                    <Space direction="vertical">
                                        <DatePicker onChange={handleDatePickerTo} />
                                    </Space>
                                </Col>
                                <Col span={4}>
                                    <label className='label-head'>Type</label>
                                    <Select
                                    showSearch
                                    placeholder="Type"
                                    defaultValue={'All'}
                                    optionFilterProp="children"
                                    className='box-form'
                                    onChange={value => setExchangeType(value)}
                                    filterOption={(input, option) =>
                                    (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                                    }
                                    options={[
                                        {
                                            value: null,
                                            label: 'All',
                                        },
                                        {
                                            value: 'Live Price',
                                            label: 'Live Price',
                                        },
                                        {
                                            value: 'Exchanged',
                                            label: 'Exchanged',
                                        },
                                    ]}
                                />
                                </Col>
                                
                                <Col span={2} className="btn">
                                <Button onClick={handleFilterClick} type="primary" ghost>Filter</Button>
                                </Col>
                            </Row>
                        </div>
                        <div className='foot'>
                            <Table className='historyTable'
                                loading={loading}
                                columns={columns}
                                dataSource={dateSource}
                                onChange
                                pagination={{
                                    pageSize: 4,
                                    total: totalPages,
                                    onChange: (page) => {
                                        fetchRecords(page, '', '', '');
                                    },
                                    position: ['left', 'bottom'],
                                }}
                            ></Table>
                        </div>
                    </div>
                </div>
            </div>        
        </Layout>


    );
}

export default App;
